<template>
    <div>
        <basic-header :items="['店铺管理','商品管理']"></basic-header>
        <el-card>
            <el-tabs v-model="activeName" @tab-click="handleTabClick">
                <el-tab-pane label="商品查看" name="first">
                    <!--表单-->
                    <el-form :inline="true" class="demo-form-inline">
                        <el-form-item label="名称">
                            <el-input  :clearable="true"  v-model="searchForm.name"></el-input>
                        </el-form-item>
                        <el-form-item label="库存状态">
                            <el-select placeholder="请选择" :clearable="true" v-model="searchForm.store">
                                <el-option
                                    v-for="item in options"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value">
                                </el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item>
                            <el-date-picker
                                    v-model="searchForm.date"
                                    type="datetimerange"
                                    range-separator="至"
                                    start-placeholder="开始日期"
                                    end-placeholder="结束日期">
                            </el-date-picker>
                        </el-form-item>
                        <el-form-item>
                            <el-button type="primary" @click="search" icon="el-icon-search" circle></el-button>
                        </el-form-item>
                    </el-form>
                    <!--表格-->

                        <el-table :data="tableData" border style="width: 100%">
                            <el-table-column prop="id" label="ID"></el-table-column>
                            <el-table-column prop="name" label="名称"></el-table-column>
                            <el-table-column prop="catname" label="分类"></el-table-column>
                            <el-table-column prop="description" label="描述"></el-table-column>
                            <el-table-column prop="thumb" label="缩略图">
                                <template #default="o">
                                    <img src="o.row.thumb" alt="" height="100px">
                                </template>
                            </el-table-column>
                            <el-table-column prop="price" label="价格"></el-table-column>
                            <el-table-column prop="store" label="库存"></el-table-column>
                            <el-table-column prop="sort" label="排序"></el-table-column>
                            <el-table-column prop="create_time" label="创建时间">
                                <template #default="o">{{o.row.create_time | formatDate}}</template>
                            </el-table-column>
                            <el-table-column prop="update_time" label="修改时间">
                                <template #default="o">{{o.row.update_time | formatDate}}</template>
                            </el-table-column>
                            <el-table-column prop="state" label="上架状态">
                                <template #default="o">
                                    <div v-if="o.row.state===1">已上架</div>
                                    <div v-else>未上架</div>
                                </template>
                            </el-table-column>
                            <el-table-column prop="operating" label="操作">
                                <template #default="o">
                                    <el-button type="text" @click="edit(o.row.id)">修改</el-button>
                                    <el-button type="text" @click="del(o.row.id)">删除</el-button>
                                </template>
                            </el-table-column>
                        </el-table>
                        <!--分页-->
                        <el-pagination
                                background
                                style="margin-top: 30px"
                                layout="prev, pager, next"
                                :current-page.sync="page"
                                :page-size.sync="pageSize"
                                :total="total"
                                @current-change="change"
                        >
                        </el-pagination>
                </el-tab-pane>
                <!--2-->
                <el-tab-pane label="商品添加" name="second">
                    <el-steps :active="active" finish-status="success">
                        <el-step title="基本信息"></el-step>
                        <el-step title="详细信息"></el-step>
                        <el-step title="配送信息"></el-step>
                    </el-steps>
                    <el-form :model="submitForm" :rules="submitRules" ref="submitForm">
                        <el-row>
                            <el-col v-if="active===0" :span="12">
                                <el-form-item label="分类">
                                    <el-select v-model="submitForm.tid" :clearable="true">
                                        <el-option
                                           v-for="item in typeOption"
                                           :key="item.id"
                                           :label="item.id"
                                           :value="item.name"></el-option>
                                    </el-select>
                                    <router-link :to="{name:'type'}">添加分类</router-link>
                                </el-form-item>
                                <el-form-item label="商品名称" prop="name">
                                    <el-input v-model="submitForm.name"></el-input>
                                </el-form-item>
                                <el-form-item label="商品介绍" prop="description">
                                    <el-input v-model="submitForm.description"></el-input>
                                </el-form-item>
                                <el-form-item label="商品价格" prop="price">
                                    <el-input type="number" v-model="submitForm.price"></el-input>
                                </el-form-item>
                                <el-form-item label="商品重量" prop="weight">
                                    <el-input type="number" v-model="submitForm.weight"></el-input>
                                </el-form-item>
                                <el-form-item label="商品库存" prop="store">
                                    <el-input type="number" v-model="submitForm.store"></el-input>
                                </el-form-item>
                                <el-form-item label="商品排序" prop="sort">
                                    <el-input type="number" v-model="submitForm.sort"></el-input>
                                </el-form-item>
                                <el-form-item label="缩略图" prop="thumb">
                                    <el-upload
                                            class="avatar-uploader"
                                            action="uploadURL"
                                            :show-file-list="false"
                                            :on-success="handleAvatarSuccess"
                                            :before-upload="beforeAvatarUpload">
                                        <img v-if="submitForm.thumb" :src="submitForm.thumb" class="avatar">
                                        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                                    </el-upload>
                                </el-form-item>
                                <el-form-item label="商品banner" prop="banner">
                                    <el-upload
                                            class="upload-demo"
                                            action="uploadURL"
                                            :before-upload="beforeAvatarUpload"
                                            :on-success="handleBannerSuccess"
                                            :on-remove="handleBannerRemove"
                                            list-type="picture"
                                            ref="banner"
                                    >
                                        <el-button size="small" type="primary">点击上传</el-button>
                                        <div slot="tip" class="el-upload__tip">只能上传jpg文件，且不超过500kb</div>
                                    </el-upload>
                                </el-form-item>
                            </el-col>
                            <el-col v-if="active===1">
                                <div ref="editor"></div>
                            </el-col>
                            <el-col v-if="active===2">
                                <el-form-item label="配送模板" prop="delivery_id">
                                    <el-select v-model="submitForm.delivery_id" :clearable="true">
                                        <el-option
                                                v-for="item in deliveryOption"
                                                :key="item.id"
                                                :value="item.id"
                                                :label="item.name">
                                        </el-option>
                                    </el-select>
                                </el-form-item>
                                <el-form-item label="是否上架">
                                    <el-switch
                                        v-model="submitForm.state"
                                        active-color="#13ce66"
                                        inactive-color="#ff4949">
                                    </el-switch>
                                </el-form-item>
                                <el-form-item>
                                    <el-button type="primary" @click="handleSubmit">提交</el-button>
                                </el-form-item>
                            </el-col>
                        </el-row>
                    </el-form>
                    <el-button-group>
                        <el-button type="primary" icon="el-icon-arrow-left" :disabled="active==0" @click="active--">上一页</el-button>
                        <el-button type="primary" :disabled="active==2" @click="active++">下一页<i class="el-icon-arrow-right el-icon--right"></i></el-button>
                    </el-button-group>
                </el-tab-pane>
            </el-tabs>
        </el-card>
        <!--弹窗-->
        <el-dialog
                title="提示"
                :visible.sync="dialogVisible"
                width="80%">
            <el-form :model="editForm" label-width="100px" :rules="submitRules" ref="editForm">
                <el-row>
                    <el-col  :span="8">
                        <el-form-item label="分类" prop="tid">
                            <el-select v-model="editForm.tid" :clearable="true">
                                <el-option v-for="item in typeOption" :key="item.id" :value="item.id"
                                           :label="item.name"></el-option>
                            </el-select>
                            <!--                            <router-link :to="{name:'type'}">添加分类</router-link>-->
                        </el-form-item>
                        <el-form-item label="商品名称" prop="name">
                            <el-input v-model="editForm.name"></el-input>
                        </el-form-item>
                        <el-form-item label="商品介绍" prop="description">
                            <el-input v-model="editForm.description"></el-input>
                        </el-form-item>
                        <el-form-item label="商品价格" prop="price">
                            <el-input type="number" v-model="editForm.price"></el-input>
                        </el-form-item>
                        <el-form-item label="商品重量" prop="weight">
                            <el-input type="number" v-model="editForm.weight" placeholder="单位为kg"></el-input>
                        </el-form-item>
                        <el-form-item label="商品库存" prop="store">
                            <el-input type="number" v-model="editForm.store"></el-input>
                        </el-form-item>
                        <el-form-item label="商品排序" prop="sort">
                            <el-input type="number" v-model="editForm.sort"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="8" :offset="6">
                        <el-form-item label="缩略图" prop="thumb">
                            <el-upload
                                    class="avatar-uploader"
                                    action="/api/upload/upload"
                                    :show-file-list="false"
                                    :on-success="handleAvatarSuccess1"
                                    :before-upload="beforeAvatarUpload">
                                <img v-if="editForm.thumb" :src="editForm.thumb" class="avatar">
                            </el-upload>
                        </el-form-item>
                        <el-form-item label="商品banner" prop="banner">
                            <el-upload
                                    class="upload-demo"
                                    action="/api/upload/upload"
                                    list-type="picture"
                                    :before-upload="beforeAvatarUpload"
                                    :on-success="handleBannerSuccess1"
                                    :on-remove="handleBannerRemove1"
                                    :file-list="fileList"
                                    ref="banner">
                                <el-button size="small" type="primary">点击上传</el-button>
                                <div slot="tip" class="el-upload__tip">只能上传jpg文件，且不超过500kb</div>
                            </el-upload>
                        </el-form-item>
                    </el-col>
                    <el-col :span="24">
                        <el-form-item prop="content">
                            <div ref="editor2"></div>
                        </el-form-item>
                    </el-col>
                    <el-col>
                        <el-form-item label="配送模板" prop="delivery_id">
                            <el-select v-model="editForm.delivery_id" :clearable="true">
                                <el-option v-for="item in deliveryOption" :key="item.id" :value="item.id"
                                           :label="item.name"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="是否上架">
                            <el-switch
                                    v-model="editForm.state"
                                    active-color="#13ce66"
                                    inactive-color="#ff4949"
                                    :active-value="1"
                                    :inactive-value="0">
                            </el-switch>
                        </el-form-item>
                    </el-col>
                </el-row>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="handleUpdate">确 定</el-button>
            </span>
        </el-dialog>
     </div>
</template>

<script>
    import Header from "../../components/Header";
    import wangeditor from "wangeditor"
    import formatDate from "../../utils/formatDate";
    export default {

        data() {

            return {
                api:"/api/goods/goods",
                activeName:"first",
                tableData:[],
                page:1,
                pageSize:5,
                total:10,
                active:0,

                options: [
                    {value: '0', label: '库存为空'},
                    {value: '1', label: '库存正常'}
                ],
                searchForm:{
                    name:'',
                    store:'',
                    date:''
                },
                typeOption:[],
                typeForm:{
                    typeOption:"",

                },
                submitForm:{
                    tid:"",
                    name:"",
                    description:"",
                    price:"",
                    sort:"",
                    thumb:"",
                    banner:"",
                    store:"",
                    content:"",
                    delivery_id:"",
                    weight:""
                },
                submitRules:{
                    tid:{required: true,message: "请输入商品分类名称",trigger: "blur"},
                    name:{required: true,message: "请输入商品名称",trigger: "blur"},
                    description:{required: true,message: "请输入商品描述",trigger: "blur"},
                    price:{required: true,message: "请输入商品价格",trigger: "blur"},
                    sort:{required: true,message: "请输入商品排序",trigger: "blur"},
                    thumb: {required: true, message: '请输入商品图片', trigger: "blur"},
                    banner: {required: true, message: '请输入商品banner', trigger: "blur"},
                    store:{required: true,message: "请输入商品库存",trigger: "blur"},
                    content:{required: true,message: "请输入商品详情",trigger: "blur"},
                    delivery_id:{required: true,message: "请输入商品费用",trigger: "blur"},
                    weight:{required: true,message: "请输入商品重量",trigger: "blur"}
                },
                uploadURL:"/api/upload/upload",
                editor:"",
                deliveryOption:[],
            }
        },
        methods: {
            fetchData(){
                let params={
                    page:this.page,
                    pageSize:this.pageSize,
                }
                if(this.searchForm.name){
                    params['name']=this.searchForm.name;
                }if (this.searchForm.store){
                    params['store']=this.searchForm.store;
                }if (this.searchForm.date){
                    //转为时间戳
                    params["start_time"]=Math.floor(this.searchForm.date[0].getTime()/1000);
                    params["end_time"]=Math.floor(this.searchForm.date[0].getTime()/1000);
                }
                // 发送请求
                this.axios.get(this.api,{
                        params
                }).then(res=>{

                    this.tableData=res.data.data;
                    this.total=res.data.total;

                });

            },
            del(id){
                this.$confirm('此操作将永久删除分类, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.axios.delete(this.api, {
                        params: {id}
                    }).then(res => {
                        if (res.data.code === 200) {
                            this.$message.success("删除成功");
                            this.fetchData();
                        }
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
            change(){
                this.fetchData()
            },
            search(){
                this.fetchData()
            },
            //步骤条
            next() {
                if (this.active++ > 2) this.active = 0;
            },
            //
            getAllType(){
                this.axios.get("/api/type/type").then(res=>{
                    if (res.data.code===200){
                        this.typeOption=res.data.data;
                    }
                })
            },
            getAllDelivery(){
                this.axios.get("/api/delivery/delivery").then(res=>{
                    if (res.data.code===200){
                        this.deliveryOption=res.data.data;
                    }
                })
            },
            handleTabClick(){
                if (this.activeName==="second"){
                    this.getAllType();
                }
            },
            //

            //图片上传
            handleAvatarSuccess(res, file) {
                this.form.thumb = res;
            },
            beforeAvatarUpload(file) {
                const isJPG = file.type === 'image/jpeg';
                const isLt500k = file.size / 1024 < 500;

                if (!isJPG) {
                    this.$message.error('上传图片只能是 JPG 格式!');
                }
                if (!isLt500k) {
                    this.$message.error('上传图片大小不能超过 500kb!');
                }
                return isJPG && isLt500k;
            },
            //banner图片上传
            handleBannerSuccess(res, file,fileList) {
                this.submitForm.banner=fileList.map(v=>v.response).join(";");
                this.$refs.submitForm.validateField("banner");
            },
            //
            handleBannerRemove(file,fileList) {
                this.submitForm.banner=fileList.map(v=>v.response).join(";");
            },
            //
            handleSubmit(){

            }


        },
        components:{
            "basic-header":Header
        },
        watch:{
            active(value){
                if(value===1&&this.editor===""){
                    this.$nextTick(()=>{
                        this.editor=new wangeditor(this.$refs.editor);
                        this.editor.customConfig.uploadImgServer='/api/upload/editorupload';
                        this.editor.customConfig.uploadFileName='file';
                        this.editor.create();
                    });
                }
            }
        },
        created() {
            this.fetchData()
        },
        mounted() {

        }
    }
</script>

<style scoped>
    .avatar-uploader .el-upload {
        border-radius: 6px;
        cursor: pointer;
        position: relative;
        overflow: hidden;
    }
    .avatar-uploader .el-upload:hover {
        border-color: #409EFF;
    }
    .avatar-uploader-icon {
        border: 1px dashed #d9d9d9;
        font-size: 28px;
        color: #8c939d;
        width: 178px;
        height: 178px;
        line-height: 178px;
        text-align: center;
    }
    .avatar {
        width: 178px;
        height: 178px;
        display: block;
    }
</style>